<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input
            v-model="filters.name"
            placeholder="用户名称"
            @keydown.enter.native="getOrders"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="getOrders">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!--列表-->
    <el-table :data="orders" highlight-current-row style="width: 100%">
      <!-- <el-table-column type="selection" width="55"> </el-table-column> -->

      <el-table-column prop="id" label="结算单id" sortable> </el-table-column>

      <el-table-column prop="settlement_method" label="结算方式" sortable>
      </el-table-column>
      <el-table-column prop="receiving_account" label="收款账号" sortable>
      </el-table-column>

      <el-table-column
        prop="actual_settlement_fee"
        label="实际结算金额"
        sortable
      >
      </el-table-column>

      <el-table-column label="结算保单" sortable>
        <template scope="scope">
          <div v-for="item in scope.row.insurancepolicy_set" :key="item.id">
            {{ item.jiaoqiang_insure_no }}  {{item.chanel_rate_id.chanel}} 
          </div>
        </template>
      </el-table-column>


      <el-table-column prop="note" label="备注" sortable> </el-table-column>

      <el-table-column label="操作" width="150" fixed="right">
        <template scope="scope">
          <!-- <el-button size="small" @click="handleEdit(scope.$index, scope.row)">查看详情</el-button>-->
          <el-button @click="handleEdit(scope.$index, scope.row)"
            >新增打款</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="pageSize"
        :total="total"
        style="float: right"
      >
      </el-pagination>

      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-sizes="[100, 200, 300, 400]"
      :page-size="100"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-col>

    <!-- 新增打款 dialog 开始  -->
    <el-dialog title="新增打款" :visible.sync="editDialog">
      <el-form
        class="demo-form-inline"
        :model="row"
        label-width="100px"
        :inline="true"
      >
        <el-form-item label="结算方式">
          {{ row.settlement_method }}
        </el-form-item>

        <el-form-item label="收款账号">
          {{ row.receiving_account }}
        </el-form-item>

        <el-form-item label="实际结算金额">
          {{ row.actual_settlement_fee }}
        </el-form-item>
        <el-form-item label="结算日期">
          {{ row.settlement_date }}
        </el-form-item>
      </el-form>
      <section>
        <h3>打款明细</h3>

        <el-form
          class="demo-form-inline"
          label-width="100px"
          :inline="true"
          v-for="(item, index) in remit"
          :key="index"
        >
          <el-form-item label="支付方式">
            {{ item.mode_of_payment }}
          </el-form-item>

          <el-form-item label="支付金额">
            {{ item.amount }} 元
          </el-form-item>

          <el-form-item label="支付账户名">
            {{ item.acount }}
          </el-form-item>

          <el-form-item label="支付账户尾号">
            {{ item.acount_tail }}
          </el-form-item>

          <el-form-item label="操作时间">
            {{ item.create_time }}
          </el-form-item>
        </el-form>
      </section>

      <section>
        <el-button @click="remittance.push({ settlement: row.id })"
          >新增打款</el-button
        >
        <el-form
          class="demo-form-inline"
          label-width="180px"
          :inline="true"
          v-for="(remit, index) in remittance"
          :key="index"
        >
          <el-form-item label="支付方式">
            <el-input v-model="remit.mode_of_payment"> </el-input>
          </el-form-item>

          <el-form-item label="支付金额">
            <el-input v-model="remit.amount" type="number"> </el-input>
          </el-form-item>

          <el-form-item label="支付账户名">
            <el-input v-model="remit.acount"> </el-input>
          </el-form-item>

          <el-form-item label="支付账户尾号">
            <el-input v-model="remit.acount_tail"> </el-input>
          </el-form-item>

          <el-button @click="subRemit(remit)">提交打款明细</el-button>
        </el-form>
      </section>

      <h3>结算保单</h3>
      <el-collapse>
        <el-collapse-item
          v-for="item in row.insurancepolicy_set"
          :key="item.id"
          :name="item.id"
          :title="`保单号` + item.jiaoqiang_insurance_no"
        >
          <div>{{ item.jiaoqiang_insurance_no }}</div>
          <div>{{ item.applicant }}</div>
        </el-collapse-item>
      </el-collapse>

      <div slot="footer" class="dialog-footer">
        <el-button @click="editDialog = false">取 消</el-button>
      </div>
    </el-dialog>

    <!-- 新增打款dialog 结束 -->
  </section>
</template>


<script>
import { accMul } from "../../util/calculate.js";
export default {
  data() {
    return {
      filters: {},
      orders: [],
      pageNum: 1,
      pageSize: 100,
      total: 0,
      addUserinfo: {
        quantity: 0,
        unit_price: 0,
      },
      addDialog: false,
      editDialog: false,
      editUserinfo: {},

      // 当前row
      row: {},
      // 打款 对象 数组  用于新增
      remittance: [],
      // 打款 明细 数组  用于显示 查询出来的 打款明细
      remit: [],
    };
  },
  created() {
    this.getOrders();
  },
  methods: {
    handleSizeChange(val) {
        this.pageSize = val
        this.getOrders()
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getOrders();
    },
    async getOrders() {
      const data = await this.$axios("Settlements/", {
        params: {
          search: this.filters.name,
          page: this.page,
          page_size: this.pageSize,
        },
      });

      console.log(data);

      this.orders = data.data;
      this.total = data.count;
    },
    async handleEdit(index, row) {
      this.row = row;

      this.getRemit();

      this.editDialog = true;

      // this.editUserinfo = Object.assign({}, row);

      // console.log(this.editUserinfo);
    },
    async subRemit(remit) {
      let data = await this.$axios.post("Remit/", remit);

      this.getRemit();
      this.$message.success(data.message);
    },
    async getRemit() {
      // 查询该结算单的 打款明细
      let { data } = await this.$axios(`Remit/?settlement=${this.row.id}`);
      this.remit = data;
    },
  },
};
</script>

<style>
</style>


